<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "highriskWork-component",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      addHighrisk: false,
      addWorkItem: {
        MainName: "", // 高風險作業主分類
        SecondName: "", // 高風險作業次分類
        Description: "", // 說明
        Classificition: "",
        Approve_Item: "0",
        Work_Type_PK: "",
      },
      addWorkRules: {
        MainName: [{ required: true, message: "必填", trigger: "blur" }],
        SecondName: [{ required: true, message: "必填", trigger: "blur" }],
      },

      highRiskWorkTableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "class_Name", label: "高風險作業主分類", width: "" },
        { id: "work_Type_Name", label: "高風險作業次分類", width: "" },
        { id: "description", label: "說明", width: "300" },
      ],
      updHighrisk: false,
      updWorkItem: {
        MainName: "", // 高風險作業主分類
        SecondName: "", // 高風險作業次分類
        Description: "", // 說明
        Classificition: "",
        Approve_Item: "",
        Work_Type_PK: "",
      },
      updWorkRules: {
        MainName: [{ required: true, message: "必填", trigger: "blur" }],
        SecondName: [{ required: true, message: "必填", trigger: "blur" }],
      },
    };
  },
  computed: {
    // 高風險種類資料 (All)
    highriskWork() {
      return this.$store.state.highrisk_all;
    },
    // 高風險種類資料 (單頁)
    highriskWorkList() {
      return this.$store.state.highrisk_allList;
    },
  },
  methods: {
    addHighriskWork() {
      this.$refs.addWorkItem.validate(async (valid) => {
        if (valid) {
          this.addWorkItem.Approve_Item = this.addWorkItem.Approve_Item
            ? "1"
            : "0";
          try {
            await this.$store.dispatch("addHighrisk", this.addWorkItem);
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "新增成功",
            });

            // 清空表單、關閉視窗，呈現表格第一頁
            this.addWorkItem = {};
            this.addHighrisk = false;
            this.$store.commit("CURRENT_PAGE_INIT");
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    // 開啟視窗，並代入相應的資訊
    updWorkHandler(item) {
      this.updWorkItem.Classificition = item.classifiCition;
      this.updWorkItem.MainName = item.class_Name;
      this.updWorkItem.SecondName = item.work_Type_Name;
      this.updWorkItem.Description = item.description;
      this.updWorkItem.Approve_Item = item.approve_Item == "1";
      this.updWorkItem.Work_Type_PK = item.work_Type_Pk;
      this.updHighrisk = true;
    },
    updHighriskWork() {
      this.$refs.updWorkItem.validate(async (valid) => {
        if (valid) {
          this.updWorkItem.Approve_Item = this.updWorkItem.Approve_Item
            ? "1"
            : "0";
          try {
            await this.$store.dispatch("updHighrisk", this.updWorkItem);
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "修改成功",
            });

            // 清空表單、關閉視窗，呈現表格第一頁
            // this.updWorkItem = {};
            this.updHighrisk = false;
            this.$store.commit("CURRENT_PAGE_INIT");
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    changePage(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "highrisk_all",
        paramsListName: "highrisk_allList",
      });
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得高風險種類
    try {
      await this.$store.dispatch("getHighrisk");
    } catch (err) {
      console.log(err);
    }
    this.$store.commit("CURRENT_PAGE_INIT");
  },
};
</script>

<template src="./highriskWork.html"></template>
